import React, { useState } from 'react';

import s from './Search.module.css';

const initialState = {
  searchActive: false,
};

const Search = () => {
  const [state, setState] = useState(initialState);
  const { searchActive } = state;

  const handleSearchToggle = () => {
    setState(({ searchActive }) => ({
      searchActive: !searchActive,
    }));
  };

  return (
    <div
      className={
        !searchActive ? [s.searchWrapper, s.active].join(' ') : s.searchWrapper
      }
    >
      <div className={s.inputHolder}>
        <input
          type="text"
          className={s.searchInput}
          placeholder="What you find?"
        />
        <button className={s.searchIcon} onClick={handleSearchToggle}>
          <span className={s.span}></span>
        </button>
      </div>
      <span className={s.close} onClick={handleSearchToggle}></span>
    </div>
  );
};

export default Search;
